import React, { useState } from "react";
import { graphql, navigate } from "gatsby";
import { signInWithEmailAndPassword } from "firebase/auth";
import { auth } from "../firebaseConfig";
import Layout from "./layout";
import { Company } from "../interfaces";
import data from "../data/data.json";
import { useTranslation } from "gatsby-plugin-react-i18next";

const info: Company = data[0];

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const { t } = useTranslation();

  const handleLogin = async (e: React.SyntheticEvent) => {
    e.preventDefault();
    try {
      const userCredential = await signInWithEmailAndPassword(
        auth,
        email,
        password
      );
      const user = userCredential.user;
      navigate(`/user/${user.uid}` as unknown as number);
    } catch (error) {
      setError((error as Error).message);
    }
  };

  return (
    <Layout data={info}>
      <div className="bg-terciary py-8 login px-4">
        <form onSubmit={handleLogin} className={styles.container} id="login">
          <h1 className={styles.title}>{t("login")}</h1>
          <div className={styles.groupItem}>
            <label htmlFor="email" className={styles.label}>
              {t("email")}
            </label>
            <input
              type="email"
              id="email"
              placeholder="Correo Electrónico"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              className={styles.input}
            />
          </div>
          <div className={styles.groupItem}>
            <label htmlFor="password" className={styles.label}>
              {t("password")}
            </label>
            <input
              type="password"
              id="password"
              placeholder="Contraseña"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className={styles.input}
              required
            />
          </div>
          <div className={styles.groupItem}>
            <button className={styles.submitButton} type="submit">
              {t("login")}
            </button>
          </div>
          {error && <p className={styles.message}>{error}</p>}
        </form>
      </div>
    </Layout>
  );
};

export default Login;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
  container:
    "bg-secondary p-8 rounded-lg shadow-md w-full max-w-md mx-auto",
  title: "text-2xl font-bold mb-6 text-dark text-center",
  label: "block text-dark font-medium",
  input:
    "w-full text-dark px-4 py-2 border border-terciary rounded-lg focus:outline-none focus:ring-2 focus:ring-primary",
  submitButton:
    "w-full bg-dark text-white py-2 px-4 rounded-lg hover:bg-terciary hover:text-dark transition",
  groupItem: "mb-4",
  message: "mt-4 text-center text-white font-medium",
};
